import React, { Dispatch, SetStateAction } from "react"
import { FileRowType } from "@rp/common/utils"
import { bigDataTheme } from "@rp/react/theme"
import { Box, Divider } from "@rp/react/web/mui/core"
import { CustomDocumentHeaderProps } from "../../DocumentViewer.types"
import { HeaderContainer } from "./PDFViewerHeader.styles"
import {
  PDFViewHeaderPages,
  PDFViewerHeaderDownloadButton,
  PDFViewerHeaderZoomOutButton,
} from "./components"
import { PDFViewerHeaderFilename } from "./components/PDFViewerHeaderFilename"
import { PDFViewerHeaderZoomInButton } from "./components/PDFViewerHeaderZoomInButton"

type PDFViewerHeaderPaneVariantProps = {
  name: string
  numPages: number
  currentPage: string | number
  scale: number
  setScale: Dispatch<SetStateAction<number>>
  documentId: string
  isPrivate: boolean
  pdfHeaderOffset?: number
  customDocumentHeaderProps: CustomDocumentHeaderProps
  data: FileRowType | undefined
}

function PDFViewerHeaderPaneVariant({
  name,
  numPages,
  currentPage,
  scale,
  setScale,
  documentId,
  isPrivate,
  customDocumentHeaderProps,
  data,
  pdfHeaderOffset,
}: PDFViewerHeaderPaneVariantProps) {
  const color = bigDataTheme.palette.text.secondary

  return (
    <HeaderContainer top={pdfHeaderOffset} height={118} backgroundColor="elevation.dp01">
      <Box sx={{ p: "16px 16px 0 16px", m: 0, width: "100%", overflow: "hidden" }}>
        <Box sx={{ display: "flex", alignItems: "center", justifyContent: "space-between" }}>
          <PDFViewerHeaderFilename data={data} name={name} />

          <Box sx={{ display: "flex", marginLeft: 0.5 }}>
            {customDocumentHeaderProps.customButtons}
          </Box>
        </Box>

        <Box sx={{ display: "flex", alignItems: "center" }}>
          <PDFViewHeaderPages currentPage={currentPage} numPages={numPages} color={color} />

          <Divider
            orientation="vertical"
            flexItem
            color="#5E5E5E"
            sx={{ mx: 2, my: "auto", height: 24 }}
          />

          <Box sx={{ display: "flex", alignItems: "center", ml: -0.75 }}>
            <PDFViewerHeaderZoomOutButton scale={scale} setScale={setScale} color={color} />
            <PDFViewerHeaderZoomInButton scale={scale} setScale={setScale} color={color} />
            <PDFViewerHeaderDownloadButton
              isPrivate={isPrivate}
              documentId={documentId}
              name={name}
              color={color}
            />
          </Box>
        </Box>
      </Box>
    </HeaderContainer>
  )
}

export { PDFViewerHeaderPaneVariant }
